<template>
    <section>
        <p class="title" data-v-entry>Hello! @TJC</p>
        <h1 data-v-entry>はじめに</h1>
        <p>
            @TJCは、2019年5月のOB会において要望としてあがった、OBがいまどこで、何をしているかを発信しあうことを実現するための情報サイトです。
        </p>

        <h1 data-v-entry>What is @TJC?</h1>
        <h2 data-v-entry>Portal</h2>
        <p>各地に散ったOBの所在や飲み会・出張等のイベントを地図上にマッピング。</p>
        <p>近くのOBやイベントを一目で確認できる！</p>
        <h2 data-v-entry>Event</h2>
        <p>飲み会等のイベントごとや次の出張予定等を登録することで、Portalの地図上にピン留めが可能</p>
        <p>コメントの投稿や、参加登録もここから。</p>

        <h2 data-v-entry>Profile</h2>
        <p>自身の近況や、SNS等の連絡先、在学時代の情報まで、自分の公開したい範囲の情報を自由に会員に発信可能。</p>

        <p style="text-align:center; font-size:250%; color:#00a7ce; margin:50px 0px">Have a good time with @TJC</p>
    </section>
</template>

<script>
export default {
    name : "Entry",
    data(){
        return {

        }
    },

    mounted()
    {
        window.scrollTo(0,0);
    }
}
</script>

<style scoped>
    p.title[data-v-entry]
    {
        font-size:300%;
        font-weight: bold;
        color:#00a7ce;
        margin: 30px 0px 10px 0px;
    }

    h1[data-v-entry]
    {
        font-size:250%;
        font-weight: normal;
        margin: 50px 0px 10px 0px;
    }

    h2[data-v-entry]
    {
        font-size:200%;
        font-weight: normal;
        margin: 20px 0px 10px 0px;
    }

    h3[data-v-entry]
    {
        font-size:150%;
        font-weight: normal;
        margin: 10px 0px 10px 0px;
    }

</style>





