<template>
    <section>
        <h1>Admin</h1>
        <h2 data-v-adminmenu>メンテナンス設定</h2>

        <div class="editBox" data-v-adminmenu>
            <h3 data-v-adminmenu>タイトル</h3>
            <input type="text" name="title" v-model="maintenanceInfo.title">

            <h3 data-v-adminmenu>概要説明</h3>
            <textarea rows="5" name="desc" v-model="maintenanceInfo.desc"></textarea>
            
            <h3 data-v-adminmenu>開始日時</h3>
            <input type="text" name="from" v-model="maintenanceInfo.from" placeholder="yyyy-MM-dd HH:mm:ss +0900">
            
            <h3 data-v-adminmenu>終了日時</h3>
            <input type="text" name="to" v-model="maintenanceInfo.to" placeholder="yyyy-MM-dd HH:mm:ss +0900">
            <div class="buttonBase" v-on:click="maintenanceRegister">登録</div>
        </div>

        <h2 data-v-adminmenu>ログインユーザー情報</h2>
        <table width="100%" data-v-adminmenu>
            <tr data-v-adminmenu>
                <th width="30%" data-v-adminmenu>loginId</th>
                <th width="30%" data-v-adminmenu>ログイン日時</th>
                <th width="30%" data-v-adminmenu>トークン有効期限</th>
            </tr>
            <tr v-for="(loginUser, index) in loginUsers" :key="index" data-v-adminmenu>
                <td data-v-adminmenu>{{loginUser.loginId}}</td>
                <td data-v-adminmenu>{{loginUser.regDt}}</td>
                <td data-v-adminmenu>{{loginUser.expDt}}</td>
            </tr>
        </table>

    </section>
</template>

<script>
export default {
    name : "AdminMenu",
    computed: {
        maintenanceInfo:{
            get()
            {
                return this.$store.getters.adminMaintenance
            },
            set(value)
            {
                this.$store.dispatch('setAdminMaintenanceFront', value)
                this.$store.dispatch('UpdateAdminMaintenance')
            }
        },
        loginUsers:{
            get()
            {
                return this.$store.getters.adminLoginUsers
            }
        }
    },

    created()
    {
        this.$store.dispatch('GetAdminMaintenance')
        this.$store.dispatch('GetAdminLoginUsers')
        
        // 管理者チェック
        if(!this.$store.getters.userInfo.admin)
        {
            this.$router.push('/')
        }
    },

    methods:{
        maintenanceRegister()
        {
            this.$store.dispatch('UpdateAdminMaintenance')
        }
    },
    
    data: function () {
        return {
        }
    }
}
</script>

<style scoped>
    h2[data-v-adminmenu] {
        font-size: 110%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 10px 0px;
    }

    h3[data-v-adminmenu] {
        font-size: 100%;
        font-weight: normal;

        margin: 30px 0px 0px 0px;
    }
    
    .editBox[data-v-adminmenu]{
        margin-bottom: 70px;
    }

    th[data-v-adminmenu]{
        text-align: left;
        font-weight: normal;
        padding : 5px 0px;
    }

    td[data-v-adminmenu] {
        padding:10px 10px;
    }


</style>


